












































html {
  height: 100%;
}

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
}

#app {
  color: #2c3e50;
  margin-top: -100px;
  max-width: 600px;
  font-family: "Source Sans Pro", Helvetica, "Microsoft YaHei", sans-serif;
  text-align: center;
}

#app a {
  color: #42b983;
  text-decoration: none;
}

.logo {
  width: 100px;
  height: 100px
}

.photo {
  width:120px;
  height:120px;
  margin: 0 auto;
  background-image:url(assets/eric.jpg);
  background-size:cover;
  border-radius:50%;
  background-repeat:no-repeat;
  background-position:center center;
}

h1.cover-heading {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue", "Microsoft YaHei", Arial,sans-serif
}

.mastfoot {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue", "Microsoft YaHei", Arial,sans-serif
}
@media (max-width: 48em) {
  .nav-masthead {
    margin-top: 10px
  }
}

@media (max-width: 40em) {
  body {
    height: auto;
  }

  .site-wrapper {
    display: block;
    /*height: auto;*/
  }

  h1.cover-heading {
    font-size: 26px
  }

  .masthead {
    margin-bottom: 1rem; 
  }

  .masthead-brand {
    font-size: 20px;
  }
}





